/* global */
.App {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	min-height: 100vh;
	background: var(--dark);
	color: var(--light);
}

/* buttons */
button {
	background: none;
	padding: 12px 24px;
	border-radius: 1px;
	width: fit-content;
}

.button-light {
	color: var(--light);
	border: 2px solid var(--light);
}

.button-light:hover {
	cursor: pointer;
	color: var(--dark);
	background: var(--light);
}

.button-dark {
	color: var(--dark);
	border: 2px solid var(--dark);
}

.button-dark:hover {
	cursor: pointer;
	color: var(--light);
	background: var(--dark);
}

/* input */
input,
textarea {
	display: flex;
	border: 0.5px solid var(--dark25);
	border-radius: 1px;
	resize: none;
	box-shadow: inset 0 0 2px var(--dark25);
	padding: 10px var(--small);
}

textarea {
	height: var(--huge);
}

/* forms */
form {
	display: flex;
	flex-direction: column;
	gap: var(--medium);
}

.form-section {
	display: flex;
	flex-direction: column;
	gap: var(--small);
}

.form-line {
	display: flex;
	gap: var(--medium);
	width: 100%;
}

.form-item {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.form-phone {
	display: flex;
	gap: var(--small);
}

.phone-three {
	width: 60px;
}

.phone-four {
	width: 70px;
}

::placeholder {
	font-weight: 200;
}

::marker {
	font-weight: 500;
}

/* misc */
svg {
	stroke-linejoin: miter;
	stroke-linecap: square;
	stroke: currentColor;
	stroke-width: 2;
	fill: none;
}

/* global body (excludes header and footer) */
.body {
	display: flex;
	flex-direction: column;
	align-items: center;
	background: var(--light);
	width: 100vw;
	z-index: 5;
	animation: fade-in 1s both 1s;
}

/* reusable components */
.brokerage-logo {
	height: 20px;
}

.icon-block {
	display: flex;
	gap: var(--small);
}

.social-icon {
	width: 20px;
}

